
#app {
      font-family: 'Avenir', Helvetica, Arial, sans-serif;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: center;
      margin-top: 60px;
}
body {
      background-color: #111629;
        color: white;
        width: auto;
        height: auto;
        /*font-family: "PathwayGothicOne";*/
}
h1 {
      font-size:400%;
}
p {
    font-size:130%;
    /*font-family: "Chivo";*/
}
a {
      text-decoration: none;
}
section {
      height: 100px;
      width: 485px
}
hr {
      margin: 0px;
}
.cv {
    color: #FEEA72;
    text-decoration: none;
}
.scroll {
    text-align: center;
  padding-top: 130px;
}
.colonne
  {
    width:485px;
    height:777px;
    background-color:#87BBD8;
}
.colonne-1
  {
  width:485px;
  height:777px;
  background-color:#F36D69;
  margin-top: 150px;
}
.colonne-2
  {
  width:485px;
  height:777px;
  background-color:#242F61;
  margin-top: 150px;
}
.colonne-3
  {
  width:485px;
  height:777px;
  background-color:#87BBD8;
}
#conteneur {
    margin: 0 auto;
    list-style: none;
    width: 1000px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
#image {
    width: 485px;
    height: 360px;
    background-image: url('assets/img/projet_1.png');
    background-size: 100%;
    background-repeat: no-repeat;
}
#image1 {
    width: 485px;
    height: 360px;
    background-image: url('assets/img/projet_2.png');
    background-size: 120%;
    background-repeat: no-repeat;
}
#image2 {
    width: 485px;
    height: 360px;
    background-image: url('assets/img/projet_3.png');
    background-size: 120%;
    background-repeat: no-repeat;
}
#image3 {
    width: 485px;
    height: 360px;
    background-image: url('assets/img/projet_4.png');
    background-size: 120%;
    background-repeat: no-repeat;
}
.ligne_verticale
  {
    border-left:1px solid white;
    height:3500px;
    margin: 90px;
}
.description {
    text-align: left;
    margin-left : 158px;
}
